<template>
  <div class="login-container">
    <div class="row login-title">이메일 로그인</div>
    <div class="input-container">
      <form class="col s12">
        <div class="row">
          <div class="input-field col s10">
            <input type="text" placeholder="이메일 주소 입력">
          </div>
          <div class="input-field col s10">
            <input type="password" placeholder="비밀번호 입력">
          </div>
        </div>
      </form>
    </div>

    <div class="row">
      <div class="col s10">
        <div class="row">
          <a class="waves-effect waves-light btn" style="width: -webkit-fill-available;">로그인</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoginForm',
  data() {
    return {
      msg: 'Welcome to Your Vue.js App',
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



